	module.exports = {"data":{"markdownRemark":{"html":"<h2>Computer Science</h2>\n<p>Computer Science is the study of computers and the concepts that make computers possible.</p>\n<p>Much of computer science was pioneered in the latter half of the 20th century.</p>\n<p>Today, if you attend an undergraduate computer science course, you will learn about both hardware and software. You’ll learn how computers work at a low level of abstraction (machine language) and at a high level of abstraction (modern scripting langauges like JavaScript).</p>\n<h1>Computer Science Fields</h1>\n<p>Computer science is categorized into several fields. The following are among the current established and well-studied fields. Most of the fields are further categorized into sub0fields.</p>\n<ul>\n<li>\n<p>Theory of Computing</p>\n<ul>\n<li>Complexity Theory</li>\n<li>Formal Methods</li>\n<li>Distributed Algorithms</li>\n</ul>\n</li>\n<li>\n<p>Security</p>\n<ul>\n<li>Cryptography</li>\n</ul>\n</li>\n<li>\n<p>Artificial Intelligence</p>\n<ul>\n<li>Data Mining</li>\n<li>Machine Learning</li>\n<li>Computer Vision</li>\n</ul>\n</li>\n<li>Software Engineering</li>\n<li>\n<p>Data Sciences</p>\n<ul>\n<li>Big Data</li>\n</ul>\n</li>\n<li>\n<p>Human Computer Interaction</p>\n<ul>\n<li>Brain Computer Interface</li>\n</ul>\n</li>\n<li>\n<p>Systems</p>\n<ul>\n<li>Distributed Systems</li>\n<li>Operating Systems</li>\n<li>Database Systems</li>\n</ul>\n</li>\n</ul>\n<h2>More Information</h2>\n<p><a href=\"http://www.cs.usfca.edu/~galles/JavascriptVisual/Algorithms.html\">Visualization of Data Structures</a></p>","fields":{"slug":"/computer-science/"},"frontmatter":{"title":"Computer Science"}}},"pathContext":{"slug":"/computer-science/"}}

